.footer {
  padding-top: 28px;
  padding-bottom: 28px;
  background: #f7f7f7;

  @media screen and (min-width: 768px) {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 29px;
    padding-bottom: 29px;
  }
}

.footer-container {
  color: #545454;
  font-size: 14px;
  line-height: 16px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  gap: 10px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 19px;
  }
}

.footer-icon {
  fill: #b92f2c;
}

.about-team {
  text-decoration: underline;
}
